<template>
    <div class="reigster_img">
        <img :src="require('../../assets/xy.png')" alt="网站Logo">
    </div>
    <div class="form">
        <form class="no_login">
            <label class="login_phone">
                <span class="iconfont">&#xe692;</span>
                <input @blur="judgePhone" v-model="phone" type="text"  maxlength="11" oninput="value=value.replace(/[^0-9.]/g,'')" placeholder="请输入手机号">
            </label>
            <label class="login_passWord">
                <span class="iconfont">&#xe623;</span>
                <input v-model="password" type="password" placeholder="请输入密码">
            </label>
            <label class="login_passWord">
                <span class="iconfont">&#xe623;</span>
                <input @blur="judgePassWord" v-model="againPassword" type="password" placeholder="确认密码">
            </label>
            <div class="yanzhenma">
                <em class="get_message" @click="getMessage">获取验证码</em>
                <input @blur="judgeMessage" v-model="againMessage" type="text" class="message" maxlength="4" oninput="value=value.replace(/[^0-9.]/g,'')" placeholder="输入验证码">
                <i>{{message}}</i>
            </div>
            <button class="login_show" @click.prevent="setUrl">注册</button>
        </form>
    </div>
</template>

<style lang="scss" scoped>
    .reigster_img{
        width: 6.25rem;
        height: 6.25rem;
        border-radius: 6.25rem;
        background-color:rgba(255, 0, 0, 0.6);
        overflow: hidden;
        text-align: center;
        margin: 7.5rem auto 0;
        img{
            width: 80%;
            height: 80%;
            margin-top: .625rem;
        }
    }

    .form{
        margin: 0 auto;
        width: 100%;
        height: 26.666667rem;
        // margin-top: 5.333333rem;
        margin-top: 1.875rem;
        .no_login{
            width: 90%;
            margin: 0 auto;
            label{
                display: block;
                width: 100%;
                height: 2.666667rem;
                margin-top: 1.25rem;
                margin-bottom: .9375rem;
                line-height: 2.666667rem;
                border: 1px solid #ccc;
                border-radius: .266667rem;
                padding: .266667rem;
                input{
                    width: 90%;
                    height: 100%;
                }
                span{
                    vertical-align: middle;
                    margin-right: .266667rem;
                    font-size: 1.25rem;
                }
            }
            button{
                width: 100%;
                height: 3.2rem;
                margin-top: 1.066667rem;
                background-color: brown;
                color: #fff;
                border-radius: .4rem;
                font-size: 1.066667rem;
            }
            .to_register{
                display: inline-block;
                width: 12.5rem;
                text-align: center;
                margin-left: 50%;
                transform: translateX(-50%);
                margin-top: 1.25rem;
            }
            .yanzhenma{
                display: flex;
                .get_message{
                    flex: 1;
                    display: inline-block;
                    height: 3.125rem;
                    width: 7.5rem;
                    background-color: brown;
                    color: #fff;
                    text-align: center;
                    line-height: 3.125rem;
                    border-radius: .625rem;
                    margin-right: .9375rem;
                }
                .message{
                    flex: 2;
                    height: 3.125rem;
                    width: 12.5rem;
                    border: 1px solid #ccc;
                    padding-left: .9375rem;
                }
                i{
                    flex: .5;
                    display: inline-block;
                    height: 3.125rem;
                    line-height: 3.125rem;
                    margin-left: .625rem;
                    color: red;
                    font-weight: bold;
                }
            }

        }
    }
</style>

<script>
import { Toast } from 'vant'
export default {
    name: 'Content',
    data () {
        return {
            // 验证码
            message: '',
            // 手机号
            phone: '',
            // 密码
            password: '',
            // 确认密码
            againPassword: '',
            // 确认验证码
            againMessage: ''
        }
    },
    methods: {
        // 获取随机验证码
        getMessage: function () {
            this.message = ''
            for (let i = 0; i < 4; i++) {
                this.message += Math.floor(Math.random() * 10)
            }
            // console.log(this.message)
        },
        // 判断电话号码是否正确
        judgePhone: function () {
            const reg = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/
            if (reg.test(this.phone)) {
                Toast('手机号正确')
            } else {
                Toast('手机号错误')
            }
        },
        // 判断密码是否正确
        judgePassWord: function () {
            if (this.password) {
                if (this.password === this.againPassword) {
                    Toast('密码通过')
                } else {
                    Toast('两次密码不一致')
                }
            }
        },
        // 判断验证码是否正确
        judgeMessage: function () {
            if (this.message) {
                if (this.message === this.againMessage) {
                    Toast('验证码正确')
                } else {
                    Toast('验证码错误')
                }
            }
        },
        // 判断页面是否跳转
        setUrl: function () {
            const reg = /^(?:(?:\+|00)86)?1(?:(?:3[\d])|(?:4[5-79])|(?:5[0-35-9])|(?:6[5-7])|(?:7[0-8])|(?:8[\d])|(?:9[189]))\d{8}$/
            if (reg.test(this.phone) && this.password === this.againPassword && this.message === this.againMessage) {
                this.$router.push('/login')
                const id = this.$store.state.user.length + 1
                this.$store.state.user.push({ id: id, phone: this.phone, password: this.password })
                console.log(this.$store.state.user)
                Toast('注册成功')
            } else {
                Toast('信息填写错误')
            }
        }
    }
}
</script>
